import React from 'react';
import Grid from '@mui/material/Unstable_Grid2';

export default function ContractInputContainer({children, xs, sm, md, lg, xl, sx, fullwidth}) {
    return (
        <Grid
            xs={fullwidth ? 12 : xs || 12 }
            sm={fullwidth ? 12 : sm || 6}
            md={fullwidth ? 12 : md || null}
            lg={fullwidth ? 12 : lg || 4}
            xl={fullwidth ? 12 : xl || null}
            sx={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'stretch',
                ...sx
            }}
        >
            {children}
        </Grid>
    )
}