import {useEffect, useState, useCallback, forwardRef} from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import ListItemText from '@mui/material/ListItemText';
import ListItem from '@mui/material/ListItem';
import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import CloseIcon from '@mui/icons-material/Close';
import Slide from '@mui/material/Slide';
import {useNavigate} from "react-router-dom";


const navList = [
    ["/pleaseregister", "Regisztrálj", "Nem ismert felhasználó"],
    ["/basicdata", "Alapadatok megadása", "Már regisztrált"],
    ["/startform", "Startform kitöltés"],
    ["/test1", "Teszt oldal"],
]

const Transition = forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

export default function HiddenMenu() {

    const navigate = useNavigate();

    const [open, setOpen] = useState(false);

    const handleKeyPress = useCallback((event) => {
        console.log(event.key)
        if (event.key === 'e' && event.ctrlKey === true) {
            event.preventDefault()
            setOpen(current => !current);
        }
    }, []);


    useEffect(() => {
        // attach the event listener
        document.addEventListener('keydown', handleKeyPress);

        // remove the event listener
        return () => {
            document.removeEventListener('keydown', handleKeyPress);
        };
    }, [handleKeyPress]);


    const toggleOpen = () => {
        setOpen(current => !current)
    }

    const handleClickOpen = () => {
        setOpen(current => !current);
    };

    const handleClose = () => {
        setOpen(current => !current);
    };

    const handleClickNavigate = (to) => {
        handleClose()
        navigate(to)
    }

    return (
        <div>
            {/*<Button variant="outlined" onClick={handleClickOpen}>*/}
            {/*    Open full-screen dialog*/}
            {/*</Button>*/}
            <Dialog
                // fullScreen
                open={open}
                onClose={handleClose}
                TransitionComponent={Transition}
            >
                <AppBar sx={{position: 'relative'}}>
                    <Toolbar>
                        <IconButton
                            edge="start"
                            color="inherit"
                            onClick={handleClose}
                            aria-label="close"
                        >
                            <CloseIcon/>
                        </IconButton>
                        <Typography sx={{ml: 2, flex: 1}} variant="h6" component="div">
                            Secret Shortcut Menu
                        </Typography>

                    </Toolbar>
                </AppBar>
                <List>
                    {
                        navList.map((item) => {
                            return (
                                <ListItem button onClick={() => handleClickNavigate(item[0])}>
                                    <ListItemText
                                        primary={item[1]}
                                        secondary={item[2] !== undefined ? item[2] : null}
                                    />
                                </ListItem>
                            )
                        })
                    }


                </List>
            </Dialog>
        </div>
    );
}
