import React from "react";
import Container from "@mui/material/Container";


export default function SectionWrapper({children, fullwidth, flipcolor, sx}) {

    let selector = flipcolor ? '&:nth-of-type(even)' : '&:nth-of-type(odd)';

    return (
        <Container
            className='section-container'
            maxWidth={false}
            disableGutters={true}
            sx={{
                flexGrow: 1,
                backgroundColor: "grey.100",
                [selector]: {
                    backgroundColor: '#fff',
                },
                ...sx
            }}>
            <Container className='section-inner-container' maxWidth={fullwidth ? false : 'xl'} sx={{
                py: 6,
            }}>
                {children}
            </Container>
        </Container>
    )
}